<template>
  <div class="todo-form">
    <span :class="['icon-ziyuan115','iconfont','icon',{'all-completed':isAllTodoCompleted}]" @click="$emit('changeAllTodo',!isAllTodoCompleted)"></span>
    <input type="text" class="inputBox" v-model.trim="value" @keydown.enter="handleEnter">
  </div>
</template>

<script>
export default {
  props:['isAllTodoCompleted',"changeAllTodo",'addTodo'],
  data(){
    return{
      value:''
    }
  },
  methods:{
    handleEnter(){
      const {value} =this;
      if(value){
        this.$emit("addTodo",value);
        this.value=''
      }
    }
  }
}
</script>

<style>
.todo-form{
  display: flex;
  justify-content: space-between;
}
  .inputBox{
    /* width: 100%; */
    height: 30px;
    flex-grow: 1;
  }
  .todo-form .icon{
    /* position: absolute; */
    line-height:34px;
    text-align: center;
    color: red;
    margin-left: 10px;
    font-size: 25px;
    margin-top:2px;
    z-index: 99;
    cursor: pointer;
  }
  .todo-form .icon.all-completed{
    color: aqua;
  }
</style>