<template>
  <div class="todo-wrap">
    <h1>todos</h1>
    <TodoForm :isAllTodoCompleted ='isAllTodoCompleted' @changeAllTodo="changeAllTodo " @addTodo='addTodo'/>
    <TodoList :todos="showTodos" :delTodo ='delTodo' :completeTodo ='completeTodo' :clear="clear"/>
    <TodoFooter @changeType='changeType' @clear ='clear'/>
    
  </div>
</template>

<script>
import TodoFooter from './TodoFooter.vue'
import TodoForm from './TodoForm.vue'
import TodoList from './TodoList.vue'
export default {
  components: { TodoForm, TodoList, TodoFooter, },
  name:'TodoWrap',
  data() {
    return {
      todos: [{
        id:'asd2asd',
        text:'666',
        isCompleted:true
      },
      {
        id:'assdwa2asd',
        text:'laotie',
        isCompleted:true
      },
      {
        id:'2sszca2asd',
        text:'niuwaniuwa',
        isCompleted:false
      },
    ],
      type:'all',
    }
  },
  methods:{
    delTodo(id){
      //  this.todos = this.todos.filter((todo) => todo.id !== id);
      const delIndex = this.todos.findIndex(todo =>todo.id ===id)
      this.todos.splice(delIndex,1)
    },
    completeTodo(id){
      const aimChange = this.todos.find(todo =>todo.id ===id)
      aimChange.isCompleted =  !aimChange.isCompleted
    },
    clear(){
      this.todos = this.todos.filter(todo =>todo.isCompleted != true)
      // console.log(this.todos.filter(todo =>todo.isCompleted === true));
    },
    changeType(newVlaue){
      this.type = newVlaue;
      console.log(this.type);
    },
    changeAllTodo(status){
    this.todos.forEach((item)=>{
      if(item.isCompleted!==status){
        item.isCompleted = status;
      }
    })
  },
  addTodo(text){
    this.todos.push({
      id:new Date().getTime(),
      text,
      isCompleted:false,
      })
      console.log(111);
  }
  },
  computed:{
    isAllTodoCompleted(){
      return this.todos.every((todo)=>todo.isCompleted);
    },
    showTodos(){
      const {type} = this
      // console.log(this.type);
      switch(type){
        case 'all' :return this.todos;
        case 'active':return this.todos.filter(todo=>todo.isCompleted !=true);
        case 'completed':return this.todos.filter(todo=>todo.isCompleted ===true);
        default: return this.todos;
     }
      
      
      // return this.todos
    }
  },
  
}
</script>

<style>
  .todo-wrap{
    width: 500px;
    margin: 0 auto;
  }
</style>