<template>
  <div class="todo-item">
    <div class="checkbox">
      <input type="checkbox" name="" :id="todo.id" @change="$emit('completeTodo',todo.id)" :checked="todo.isCompleted"/>
      <label :for="todo.id"></label>
    </div>
    <span :class="{'todo-text':true ,'active':todo.isCompleted }">{{todo.text}}</span>
    <span @click="$emit('delTodo',todo.id)" class="del">x</span>
    
  </div>
</template>

<script>
export default {
props:['todo']
}
</script>

<style>
  .todo-item{
    display: flex;
    justify-content: space-between;
    border-bottom:1px solid #ccc ;
    padding: 10px;
  }
  .todo-text{
    flex-grow: 1;
    margin-left: 15px;
  }
  .todo-text.active{
    text-decoration: line-through;
    color: #ccc;
  }
  .todo-item .del{
    cursor: pointer;
    margin-right: 10px;
  }
  .todo-item .checkbox input{
    display: none;
  }
  .todo-item .checkbox label{
    display: block;
    width: 30px;
    height: 30px;
    background-image: url("../assets/2.svg");
    background-size: contain;
  }
  .todo-item .checkbox input:checked +label{
    background-image: url("../assets/1.svg");
  }
</style>